import { Component } from '@angular/core';
import { LoginService } from './service/login.service';
import { LoginModel } from './model/login.model';

@Component({
  providers: [LoginService],
  selector: 'app-login',
  templateUrl: './login.component.html'
})

export class LoginComponent {
  public errorMessage: string;
  constructor(private loginService: LoginService) { }
  loginSubmit(event: LoginModel) {
    // this.loginService.loginApi(event);
    this.errorMessage = "";
    this.loginService.loginApi2(event).catch((res) => {
      this.setErrorLoginMessage(res);
    });
  }

  setErrorLoginMessage(res) {
    if (res.status === 401) {
      this.errorMessage = "ชื่อผู้ใช้หรือรหัสผ่านไม่ถูกต้อง";
    } else if (res.status === 404 || res.status === 0) {
      this.errorMessage = "ไม่พบการเชื่อมต่อ";
    } else if (res.status === 400 ||
      res.status === 422 ||
      res.status === 500 ||
      res.status === 403) {
      this.errorMessage = "พบข้อผิดพลาด กรุณาติดต่อผู้ดูแลระบบ";
    }
  }
}



