import { Component, Output, Input } from '@angular/core';
import { EventEmitter } from '@angular/core';
import { NgForm } from '@angular/forms';
import { LoginModel } from '../../../containers/login/model/login.model';

@Component({
  selector: 'app-login-component',
  templateUrl: './login.form.component.html',
  styleUrls: ['./login.form.component.scss']
})
export class LoginFormComponent {
  @Output() login: EventEmitter<LoginModel> = new EventEmitter();
  @Input() errorMessage: string;
  public username;
  public password;

  loginSubmit(data: NgForm) {
    const loginModel: LoginModel = new LoginModel(data.value.username, data.value.password);
    this.login.emit(loginModel);
  }
}



