import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { AuthenticationService } from '../../../services/user/authentication-service.service';
import { LoginModel } from '../model/login.model';

@Injectable()
export class LoginService {

    constructor(private http: HttpClient, private authenticationService: AuthenticationService) { }
    loginApi(data: LoginModel) {
        return this.authenticationService.login(data.username, data.password);
    }

    loginApi2(data: LoginModel) {
        return this.authenticationService.login2(data.username, data.password);
    }
}



